import { useEffect, useRef } from 'react'

import { Box, Button, Stack, Typography } from '@mui/material'
import type { Editor } from '@toast-ui/react-editor'

import type { ClubId } from 'src/models/club'

import TextEditor from 'src/components/TextEditor'

import { useGetClubContent } from 'src/hooks/api/club'
import { useEditClubContent } from 'src/hooks/api/settings'

const ClubContent: React.FC<{ clubId: ClubId }> = ({ clubId }) => {
  const editorRef = useRef<Editor>(null)
  const { data: clubContent, isSuccess } = useGetClubContent(clubId)
  const editClubContent = useEditClubContent()

  const handleClickEditButton: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (isSuccess) {
      const newContent = editorRef.current?.getInstance().getHTML() || ''
      editClubContent.mutate({ clubId, content: newContent })
    }
  }

  useEffect(() => {
    if (isSuccess && clubContent && editorRef.current) {
      editorRef.current.getInstance().setHTML(clubContent.content)
    }
  }, [isSuccess, clubContent])

  return (
    <Box mb={2} width={'100%'}>
      <Stack spacing={2}>
        <Typography variant={'h4'}>동아리 소개</Typography>
        <TextEditor
          initialValue=""
          editorRef={editorRef}
          toolbarItems={[
            ['heading', 'bold', 'italic', 'strike'],
            ['hr', 'quote'],
            ['ul', 'ol', 'task'],
            ['table', 'image', 'link'],
            ['code', 'codeblock']
          ]}
        />
        <Box display="flex" justifyContent="flex-end" mt={2}>
          <Button variant="contained" color="success" onClick={handleClickEditButton}>
            등록 / 수정
          </Button>
        </Box>
      </Stack>
    </Box>
  )
}

export default ClubContent
