import { useEffect, useState } from 'react'

import type { SelectChangeEvent } from '@mui/material'
import { Box, Typography } from '@mui/material'

import type { ClubId } from 'src/models/club'

import ClubSettingsField from 'src/components/ClubSettingsField'

import { useGetClubInformation } from 'src/hooks/api/club'
import type { ClubInformationRequestData } from 'src/hooks/api/settings'
import { useEditClubInfo } from 'src/hooks/api/settings'

const ClubInformation: React.FC<{ clubId: ClubId }> = ({ clubId }) => {
  const { data: clubData, isSuccess } = useGetClubInformation(clubId)

  const [clubInformation, setClubInformation] = useState<ClubInformationRequestData>({
    description: '',
    category: '',
    belonging: '',
    location: ''
  })

  const editClubLinks = useEditClubInfo()

  // TODO: useEffect보다 더 효과적인 방법 구현하기..
  useEffect(() => {
    if (isSuccess && clubData) {
      setClubInformation({
        category: clubData.category ?? '',
        belonging: clubData.belonging ?? '',
        description: clubData.description ?? '',
        location: clubData.location ?? ''
      })
    }
  }, [isSuccess, clubData])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setClubInformation(prevState => ({
      ...prevState,
      [name]: value
    }))
  }

  const handleSelect = (e: SelectChangeEvent<string>) => {
    const { name, value } = e.target
    setClubInformation(prevState => ({
      ...prevState,
      [name]: value
    }))
  }

  const handleClickEditButton: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (isSuccess) {
      editClubLinks.mutate({ clubId, clubInfo: clubInformation })
    }
  }

  return (
    <Box mb={2}>
      <Box width={'100%'}>
        <Typography variant={'h4'}>동아리 기본 정보 설정</Typography>
        <ClubSettingsField
          clubInformation={clubInformation}
          handleChange={handleChange}
          handleSelect={handleSelect}
          handleClickEditButton={handleClickEditButton}
          fixedClubName={clubData?.name}
        />
      </Box>
    </Box>
  )
}

export default ClubInformation
